export class GroupFilter {
  id?: string;
  systemId?: string;
  nameEN?: string;
  nameTC?: string;
  nameSC?: string;
  equipmentIds?: string[];

  constructor(options?: {
    id?: string;
    systemId?: string;
    nameEN?: string;
    nameTC?: string;
    nameSC?: string;
    equipmentIds?: string[];
  }) {
    this.id = options.id ? options.id : new Date().getTime().toString();
    this.systemId = options?.systemId;
    this.nameEN = options?.nameEN;
    this.nameTC = options?.nameTC;
    this.nameSC = options?.nameSC;
    this.equipmentIds = [];
  }
}
