import { Component, Input } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AddLampDialogComponent } from './add-lamp-dialog/add-lamp-dialog.component';
import { LampData } from '../lamp-data';
import { LampSelect } from '../lamp-select';
import { Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-add-lamp',
  templateUrl: './add-lamp.component.html',
  styleUrls: ['./add-lamp.component.css']
})
export class AddLampComponent {
  @Input() lampDataList = [];
  dialogRef?: MatDialogRef<AddLampDialogComponent>;
  

  availableLampData: LampSelect[] = [
    new LampSelect('Lamp ON', false),
    new LampSelect('Lamp Off', false),
    new LampSelect('Normal', false),
    new LampSelect('Fault', false),
    new LampSelect('Unknown', false),
    new LampSelect('Power Failure', false)
  ];

  @Output() updateLampData = new EventEmitter<LampData[]>();

  constructor(private dialog: MatDialog) { }

  openAddLampDialog() {
    this.dialogRef = this.dialog.open(AddLampDialogComponent, { panelClass: 'add-lamp', 
    data: { 'selectionList' : this.availableLampData }, 
    width: '50%', 
    minWidth: '500px', 
    autoFocus: false, });

    this.dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);

      if (result!= null) {
        const lampData = result;

        this.lampDataList.push(lampData);
        //mark selected. Shortlisted the selected ones
        this.availableLampData.find(element => element.name == lampData.lampSelect.name).selected = true;

        //notify add / edit equipment for the changes
        this.updateLampData.emit(this.lampDataList);
      }
    });
  }

  // remove(data: LampData) {
  //   const index = this.lampDataList.indexOf(data, 0);
  //   this.lampDataList.splice(index, 1);
  // }

  editLamp(lampData: LampData) {
    this.dialogRef = this.dialog.open(AddLampDialogComponent, { panelClass: 'add-lamp', 
    data: { 'selectionList' : this.availableLampData, 'lampData' : lampData }, 
    width: '50%', 
    minWidth: '500px', 
    autoFocus: false, });

    this.dialogRef.afterClosed().subscribe(result => {
      console.log(`Dialog result: ${result}`);

      if (result!= null) {
        const lampData = result;

        let index = this.lampDataList.findIndex(m => m.lampSelect.name === lampData.lampSelect.name);
        
        if (lampData.markDelete == true) {
          //find the delete entry
          if (index != -1) {
            this.lampDataList.splice(index, 1);

            this.updateLampData.emit(this.lampDataList);
          }
        } else { 
          //find and replace the existing one
          // let index = this.lampDataList.findIndex(m => m.lampSelect.name === lampData.lampSelect.name);
          if (index != -1) {
            this.lampDataList[index] = lampData;

            this.updateLampData.emit(this.lampDataList);
          }
          //mark selected. Shortlisted the selected ones
          this.availableLampData.find(element => element.name == lampData.lampSelect.name).selected = true;
        } 
      }
    });
  }
}
