import { SystemData } from "./system-data";
import { LampData } from "./system/add-equipment/lamp-data";

export class EquipmentSetting {
    processid?: string;
    detail?: SystemDetail[];
    result?: string;
    username?: string;

    constructor(options?: {
        processid?: string;
        detail?: SystemDetail[];
        result?: string;
        username?: string;
    }) {
        this.processid = options?.processid;
        this.detail = options?.detail ?? [];
        this.result = options?.result;
        this.username = options?.username;
    }
}

export class SystemDetail {
    order?: number;
    systemID?: string;
    system_EN?: string;
    system_CN?: string;
    system_SC?: string;
    equipments?: Equipment[];

    constructor(options?: {
        order?: number;
        systemID?: string;
        system_EN?: string;
        system_CN?: string;
        system_SC?: string;
        equipments?: Equipment[];
    }) {
        this.order = options?.order;
        this.systemID = options?.systemID;
        this.system_EN = options?.system_EN;
        this.system_CN = options?.system_CN;
        this.system_SC = options?.system_SC;
        this.equipments = options?.equipments;
    }
}

export class Equipment {
    floor?: string;
    name_EN?: string;
    name_CN?: string;
    name_SC?: string;
    designation?: string;
    plc_ID?: number;
    point?: Map<string, number>;
    field?: Map<string, string>;
    do_Type?: Map<string, string>;
    reverse?: Map<string, string>;
    alarm_Point?: Map<string, number>;
    //
    order?: number;
    systemId?: string;
    lamp: LampData[];
    groupFilterIds?: string[];

    constructor(options?: {
        floor?: string;
        name_EN?: string;
        name_CN?: string;
        name_SC?: string;
        designation?: string;
        plc_ID?: number;
        point?: Map<string, number>;
        field?: Map<string, string>;
        do_Type?: Map<string, string>;
        reverse?: Map<string, string>;
        alarm_Point?: Map<string, number>;
        order?: number;
        systemId?: string;
        lamp: LampData[];
        groupFilterIds: string[];
    }) {
        this.floor = options?.floor;
        this.name_EN = options?.name_EN;
        this.name_CN = options?.name_CN;
        this.name_SC = options?.name_SC;
        this.designation = options?.designation;
        this.plc_ID = options?.plc_ID;
        this.point = options?.point;
        this.field = options?.field;
        this.do_Type = options?.do_Type;
        this.reverse = options?.reverse;
        this.alarm_Point = options?.alarm_Point;
        this.order = options?.order;
        this.systemId = options?.systemId;
        this.lamp = options?.lamp;
        this.groupFilterIds = options?.groupFilterIds;
    }
}