// book.service.ts

import { Injectable } from '@angular/core';

@Injectable({ providedIn: 'root' })
export class UserService {
  private userInfo?: User | undefined;
  private loginInfo?: LoginInfo | undefined;

    discardUserLogin() {
        this.userInfo = undefined;
        this.loginInfo = undefined;
    }

    getUserInfo() {
        return this.userInfo;
    }

    getLoginInfo() {
        return this.loginInfo;
    }

  saveUserLogin({mUsername, mUserLevel, processId, authResult, loginID}: NamedParameters) {
    this.userInfo = {name: mUsername, level: mUserLevel};
    this.loginInfo = {user: this.userInfo, processId: processId, authResult: authResult, id: loginID};

    // localStorage.setItem('loginInfo', JSON.stringify(this.loginInfo));
    sessionStorage.setItem('loginInfo', JSON.stringify(this.loginInfo));
  }

  saveLoginInfo(loginInfo: LoginInfo) {
    this.loginInfo = loginInfo;
  }

}

interface NamedParameters {
    mUsername: String;
    mUserLevel: number;
    processId: String;
    authResult: String;
    loginID: String;
}

export type User = {
    name: String;
    level: number;
}

export type LoginInfo = {
    user: User;
    processId: String;
    authResult: String;
    id: String;
}