  getWindowWidth() {
    console.log('window width', window.innerWidth);
    if (window.innerWidth > 1000) {
      this.windowWidth = window.innerWidth;
    } else {
      this.windowWidth = 1000;
    }
  }

  getSystemNameFromId(systemId: string) {
    let system = this.systemList.find((s) => s.id == systemId);
    if (system) {
      return system.nameEN;
    }
    return '*Unclassfied';
  }

  constructor(
    @Inject(WebsocketService) private WebSocketService: WebsocketService,
    private userService: UserService,
    private authService: AuthService,
    private router: Router,
    private modalService: NgbModal,
    private dialog: MatDialog,
    private translate: TranslateService,
    private snackbar: MatSnackBar
  ) {
    let groupFilters = SystemUtils.getGroupFilterListFromLocalStorage();
    this.dataSource = new MatTableDataSource(groupFilters);

    this.systemList = SystemUtils.getListFromLocalStorage();

    let savedLang = localStorage.getItem('LANG');
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }

    console.log('production? : ' + environment.production);
    this.isDev = !environment.production;

    WebSocketService.reconnect();
    console.log('++++++++ reconnect websocket ++++++++');
    WebSocketService.messages.subscribe((msg) => {
      console.log('[admin] Response from websocket: ' + msg);

      if (!this.isDev) {
        this.updateSessionTimer();
      }

      if (AppComponent.isCallingOnClose) {
        //reset
        // setTimeout(() => {this.isCallingOnClose = false;},200);
        AppComponent.isCallingOnClose = false;
      }

      if (isJsonString(msg.toString())) {
        var jObject = JSON.parse(msg.toString());
        let timeNow = new Date();
        const result = timeNow.toLocaleTimeString('en-GB', {
          // you can use undefined as first argument
          hour: '2-digit',
          minute: '2-digit',
          second: '2-digit',
        });
        this.dateTime = result;
        // this.dateTime = timeNow.getHours() + ":" + timeNow.getMinutes() + ":" + timeNow.getSeconds();

        if (jObject.type == Constants.GET_PROJECT_NAME_RESULT) {
          if (jObject.content) {
            let projectDetail = jObject.content['detail'];
            if (Object.keys(projectDetail).length !== 0) {
              let project = new Project();
              //convert json to Project
              Object.assign(project, jObject.content);

              //parent
              this.project = project;
            } else {
              console.log("it has no project detail");

              this.closeWebsocket(ChangeProjectComponent.route);
            }
          }
        } else if (jObject.type == Constants.GET_EQUIPMENT_SETTING_RESULT) {
          if (jObject.content) {
            console.log('[WS]Get Equipment Setting Result : ', jObject.content);
            let eSetting = new EquipmentSetting();
            //convert json to EquipmentSetting
            Object.assign(eSetting, jObject.content);

            this.equipmentSetting = eSetting;

            //save to local storage
            EquipmentUtils.saveEquipmentSettingToLocalStorage(eSetting);
          }
        }
      }
    });

    /*addEventListener("resize", (event) => {
      this.getWindowWidth();
    });*/
  }

  openSnackBar(message: string, action: string) {
    this.snackbar.open(message, action, {
      panelClass: 'my-snackbar',
      duration: 5000,
    });
  }

  createNew(id: string) {
    switch (id) {
      case 'equipment':
        this.closeWebsocket(AddEquipmentComponent.route, { edit: false });
        break;
      case 'system':
        this.closeWebsocket(AddSystemComponent.route);
        break;
      case 'group_filter':
        this.closeWebsocket(AddGroupFilterComponent.route);
        break;
      default:
        this.openSnackBar('Coming Soon', 'OK');
    }
  }

  editExisting(id: string) {
    switch (id) {
      case 'project':
        this.closeWebsocket(ChangeProjectComponent.route);
        break;
      case 'system_list':
        this.closeWebsocket(SystemListComponent.route);
        break;
      case 'equipment_list':
        this.closeWebsocket(ListEquipmentComponent.route);
        break;
      case 'group_filter':
        this.closeWebsocket(GroupFilterListComponent.route);
        break;
      default:
        this.openSnackBar('Coming Soon', 'OK');
    }
  }

  updateAlignment(numOfSystems: number) {
    if (numOfSystems != undefined && numOfSystems > 5) {
      return '0px';
    } else {
      return 'auto';
    }
  }

  ngOnDestroy(): void {
    if (this.timer) {
      clearTimeout(this.timer);
    }
  }

  updateSessionTimer() {
    if (this.timer !== null) {
      clearTimeout(this.timer);
      // console.warn("clear 10 seconds timeout")
    }
    // console.error("set 10 seconds timeout")
    this.timer = setTimeout(() => {
      console.log('60 seconds over');
      this.showSessionTimeoutDialog();
    }, 60000);
  }

  showSessionTimeoutDialog() {
    this.remindLoginRef = this.dialog.open(RemindLoginDialogComponent, {
      panelClass: 'show-device',
    });

    this.remindLoginRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
      if (!result || result == undefined) {
        this.updateSessionTimer();
      } else {
        this.logout();
      }
    });
  }

  originalOrder = (
    a: KeyValue<string, Object>,
    b: KeyValue<string, Object>
  ): number => {
    return 0;
  };

  goToUploadSummary() {
    alert('Go to Upload Summary');
  }

  ngOnInit() {
    this.browserRefresh = browserRefresh;
    console.log('refreshed?:', browserRefresh);

    this._getUserInfo();

    if (this.showMenu == false) {
      setTimeout(() => {
        this.showMenu = true;
      }, 2000);
    }

    setTimeout(() => {
      if (browserRefresh) {
        let stored = sessionStorage.getItem('loginInfo');
        if (stored !== undefined) {
          console.log('execute webSocket_Connected after refresh [admin]');
          let loginInfo: LoginInfo = JSON.parse(stored!);
          this.userService.saveLoginInfo(loginInfo);
          this.sendWSConnected();
        }
        // this.sendWSOnClose(browserRefresh);
      } else {
        if (this.userService.getLoginInfo() != undefined) {
          console.log('execute webSocket_Connected after 1 sec [admin]');
          this.sendWSConnected();
        }
        //wait for a second
      }
    }, 1000);

    window.addEventListener(
      'beforeunload',
      () => {
        //call when user clicked refresh
        if (!AppComponent.isCallingOnClose) {
          AppComponent.isCallingOnClose = true;

          console.log('Message onClose (admin)');
          //TODO: can't differentiate between refresh and close window
          //this is admin
          this.sendWSOnClose();
        }
      },
      false
    );

    window.addEventListener(
      'popstate',
      () => {
        //back button clicked
        if (!AppComponent.isCallingOnClose) {
          AppComponent.isCallingOnClose = true;
          this.closeWebsocket();
        }
      },
      false
    );
  }

