  scrollToTop(): void {
    // scroll to the top of the body
    return document.body.scrollIntoView({
      behavior: 'smooth',
      block: 'start',
      inline: 'start',
    });
  }

  onScroll(e: Event) {
    let scrollExtent = (e.target as HTMLElement).scrollLeft;
    // console.log(scrollExtent)

    //make system header and content scroll in the same extent
    let content = document.getElementById('content')!;
    content.scrollTo({
      left: scrollExtent,
      // behavior: "smooth",
    });
  }

  sendWSOnClose(navigateTo?: any) {
    this.browserRefresh = !browserRefresh;
    let queryMessage: Message = {
      type: '',
      content: {},
    };
    queryMessage.type = 'CloseEvent';
    queryMessage.content = {
      F5Refresh: '',
    };
  }

  public open(modal: any): void {
    this.modalService.open(modal);
  }

  sendWSConnected() {
    let loginMessage: Message = {
      type: '',
      content: {},
    };

    var loginInfo: LoginInfo | undefined = this.userService.getLoginInfo();
    loginMessage.type = 'webSocket_Connected';
    // loginMessage.content = "Client Connected";
    loginMessage.content = {
      message: 'Client Connected',
      username: loginInfo?.user.name,
      userlevel: loginInfo?.user.level,
      loginID: loginInfo?.id,
    };

    this.WebSocketService.messages.next(loginMessage);

    this.download();
  }

