  //if user click Download / Sync button, withDownload = true
  download(options? : {withDownload: boolean}) {
    console.log('download');
    // alert('download');
    this._getProjectName();
    if (options?.withDownload) {
      this._getEquipmentSetting();
    }
    this._getGroupSetting();
    this._getLampList();
  }
