import { Component } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Location } from '@angular/common';
import { AddEquipmentSubmitDialogComponent } from './add-equipment-submit-dialog/add-equipment-submit-dialog.component';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ConvertUtils } from 'src/app/convert-utils';
import { SampleDeviceData } from 'src/app/sample-device-data';
import { EquipmentUtils } from '../equipment-utils';
import { ConfirmDeleteSystemDialogComponent } from '../confirm-delete-system-dialog/confirm-delete-system-dialog.component';
import { RouterExtService } from 'src/app/service/route.service';
import { SystemUtils } from '../system-utils';
import { SampleSystemData } from 'src/app/sample-system-data';
import { GroupFilter } from '../group-filters/group-filter';
import { LampData } from './lamp-data';
import { Equipment, SystemDetail } from 'src/app/equipment-setting';

@Component({
  selector: 'app-add-equipment',
  templateUrl: './add-equipment.component.html',
  styleUrls: ['./add-equipment.component.css'],
})
export class AddEquipmentComponent {
  updateLampData(newLampList: LampData[]) {
    this.lampDataList = newLampList;
  }

  static route = 'equipment-setup';

  edit = false;
  device?: SampleDeviceData;

  showMenu = true;
  siteLanguage = 'English';
  languageList = [
    { code: 'en', label: 'English' },
    { code: 'zh_HK', label: '繁體中文' },
    { code: 'zh_CN', label: '简体中文' },
  ];
  currentLanguage?: String;

  lampDataList = [];

  // groupSelection = new FormControl('');
  // groupSelectionList: string[] = ['Critical System', 'Operation', 'Spare Kit', 'Special'];
  private dialogRef?: MatDialogRef<AddEquipmentSubmitDialogComponent>;
  private confirmDialogRef?: MatDialogRef<ConfirmDeleteSystemDialogComponent>;

  addEquipmentForm: FormGroup;
  previousUrl?: string;

  systemList: SampleSystemData[];
  groupFilterList: GroupFilter[];
  previousSystemId: number = -1;

  get f() {
    return this.addEquipmentForm;
  }

  sortArray(systemList: SampleSystemData[]) {
    systemList.sort((a, b) => {
      if (a.order > b.order) {
        return 1;
      }
      if (a.order < b.order) {
        return -1;
      }
      return 0;
    });

    //reassign the order in the system object
    this.systemList = SystemUtils.reassignOrder(systemList);
  }

  filterBySystem(list) {
    return list.filter((filter) => {
      return filter.systemId == this.f.get('systemId').value;
    });
  }

  ngOnInit() {
    let systemArr = SystemUtils.getListFromLocalStorage();
    this.sortArray(systemArr);

    if (systemArr.length == 0) {
      this.f.get('systemId').disable();
    } else {
      this.f.get('systemId').enable();
    }

    this.groupFilterList = SystemUtils.getGroupFilterListFromLocalStorage();

    this.systemList.forEach((system) => {
      if (system.id == undefined) {
        system.id = new Date().getTime().toString();
        SystemUtils.updateItemToList(system, 'nameEN');
      }
      // console.log("EN:",system.nameEN,"; TC:",system.nameTC,"; CN:",system.nameSC,"; id:",system.id,"; order:",system.order);
    });

    if (this.filterBySystem(this.groupFilterList).length == 0) {
      this.f.get('groupFilter').disable();
    } else {
      this.f.get('groupFilter').enable();
    }

    //Listen to form changes
    // this.f.valueChanges.subscribe(data => {
    //   console.log('previousSysemId', this.previousSystemId)
    //   console.log('System changes', data.systemId)

    //   if (data.systemId != this.previousSystemId) {
    //     this.previousSystemId = data.systemId;
    //     //Clear group filter field if other systems selected
    //     console.log("system changes detected! clear group filter")

    //     this.f.get("groupFilter")?.reset();
    //   }
    // });

    this.f.get('systemId').valueChanges.subscribe((data) => {
      console.log('SystemId changes', data);

      if (data != this.previousSystemId) {
        this.previousSystemId = data;
        //Clear group filter field if other systems selected
        console.log('system changes detected! clear group filter');

        this.f.get('groupFilter')?.reset();

        if (this.filterBySystem(this.groupFilterList).length == 0) {
          this.f.get('groupFilter').disable();
        } else {
          this.f.get('groupFilter').enable();
        }
      }
    });
  }

  constructor(
    private translate: TranslateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private location: Location,
    private routerExtService: RouterExtService,
    private dialog: MatDialog
  ) {
    let savedLang = localStorage.getItem('LANG');
    if (savedLang != null) {
      this.siteLanguage = savedLang;
      translate.use(savedLang);
      this.currentLanguage = translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }

    //get previous navigation
    this.routerExtService.getPreviousUrl().then((value) => {
      this.previousUrl = value;
      console.log('previousUrl', this.previousUrl);
    });

    this.addEquipmentForm = new FormGroup({
      systemId: new FormControl(),
      nameEN: new FormControl(),
      nameTC: new FormControl(),
      nameSC: new FormControl(),
      floor: new FormControl(),
      designation: new FormControl(),
      groupFilter: new FormControl(),
    });

    //To use "edit", use {queryParams : {'edit':true}} in navigator
    this.edit = this.activatedRoute.snapshot.queryParams['edit'] ?? false;
    console.log('edit :', this.edit);
    if (this.edit) {
      this.device = JSON.parse(
        this.activatedRoute.snapshot.queryParams['device']
      );
      //prefill the data
      this.f.get('systemId').setValue(this.device?.systemId);
      this.f.get('nameEN').setValue(this.device?.nameEN);
      this.f.get('nameTC').setValue(this.device?.nameTC);
      this.f.get('nameSC').setValue(this.device?.nameSC);
      this.f.get('floor').setValue(this.device?.floor);
      this.f.get('designation').setValue(this.device?.designation);
      if (this.f.get('groupFilter') != null)
        this.f.get('groupFilter').setValue(this.device?.groupFilterIds);
      this.lampDataList = this.device?.lamp;
    }
  }

  changeSiteLanguage(localeCode: string): void {
    const selectedLanguage = this.languageList
      .find((language) => language.code === localeCode)
      ?.label.toString();

    if (selectedLanguage) {
      this.siteLanguage = selectedLanguage;
      this.translate.use(localeCode);
      console.log('localCode', localeCode);
      localStorage.setItem('LANG', localeCode);
    }
    setTimeout(() => {
      this.currentLanguage = this.translate.currentLang;
      console.log('currentLanguage', this.currentLanguage);
    }, 300);
  }

  convertToSimplifiedChinese() {
    const _nameTC = this.f.get('nameTC')?.value;
    this.f
      .get('nameSC')
      ?.setValue(ConvertUtils.convertToSimplifiedChinese(_nameTC));
  }

  openSubmittedDialog() {
    this.dialogRef = this.dialog.open(AddEquipmentSubmitDialogComponent, {
      width: '450px',
    });

    this.dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
      if (result != null) {
        if (result['reset'] == true) {
          this.f.reset();
        } else if (result['path'] != null) {
          if (
            this.previousUrl == '/admin' ||
            this.previousUrl == '/equipment-setup'
          ) {
            // Navigating to a new page ('/system-list') and replacing the current page ('/system-setup')
            this.router.navigateByUrl(result['path'], { replaceUrl: true });
          } else {
            //Navigate back to previous page ('/system-list')
            this.location.back();
          }
        }
      }
    });
  }

  submitForm() {
    console.log(this.f.get('systemId')?.value);
    console.log(this.f.get('nameEN')?.value);
    console.log(this.f.get('nameTC')?.value);
    console.log(this.f.get('nameSC')?.value);
    console.log(this.f.get('floor')?.value);
    console.log(this.f.get('designation')?.value);

    //custom validation
    if (
      this.f.get('systemId')?.value == '' ||
      this.f.get('systemId')?.value == null
    ) {
      this.f.get('systemId')?.setErrors({ required: true });
    }
    if (
      this.f.get('nameEN')?.value == '' ||
      this.f.get('nameEN')?.value == null
    ) {
      this.f.get('nameEN')?.setErrors({ required: true });
    }
    if (
      this.f.get('nameTC')?.value == '' ||
      this.f.get('nameTC')?.value == null
    ) {
      this.f.get('nameTC')?.setErrors({ required: true });
    }
    if (
      this.f.get('nameSC')?.value == '' ||
      this.f.get('nameSC')?.value == null
    ) {
      this.f.get('nameSC')?.setErrors({ required: true });
    }
    if (
      this.f.get('floor')?.value == '' ||
      this.f.get('floor')?.value == null
    ) {
      this.f.get('floor')?.setErrors({ required: true });
    }
    if (
      this.f.get('designation')?.value == '' ||
      this.f.get('designation')?.value == null
    ) {
      this.f.get('designation')?.setErrors({ required: true });
    }

    if (this.f.invalid) {
      //error thrown if fields are empty
      return;
    }

    console.log('group filter ids : ', this.f.get('groupFilter')?.value);
    //TODO: replace
    let equipments = EquipmentUtils.getListFromLocalStorage();

    let setting = EquipmentUtils.getEquipmentSettingToLocalStorage();
    let equipment_list = EquipmentUtils.getEquipmentBySystemId(this.f.get('systemId')?.value) as Equipment[];

    if (this.device == null) {
      //Add new system
      //TODO : replace
      let newOrder = EquipmentUtils.getListFromLocalStorage().length + 1 ?? 1;
      
      let newOrder2 = equipment_list.length + 1 ?? 1;
      
      //TODO : replace
      let newDevice = new SampleDeviceData({
        floor: this.f.get('floor')?.value,
        designation: this.f.get('designation')?.value,
        nameEN: this.f.get('nameEN')?.value,
        nameTC: this.f.get('nameTC')?.value,
        nameSC: this.f.get('nameSC')?.value,
        order: newOrder,
        systemId: this.f.get('systemId')?.value,
        lamp: this.lampDataList,
        groupFilterIds: this.f.get('groupFilter')?.value,
      });

      //Save User Inputs to New 'Equipment' object
      let newEquipment = new Equipment({
        floor: this.f.get('floor')?.value,
        name_EN: this.f.get('nameEN')?.value,
        name_CN: this.f.get('nameTC')?.value,
        name_SC: this.f.get('nameSC')?.value,
        designation: this.f.get('designation')?.value,
        //
        order: newOrder2,
        systemId: this.f.get('systemId')?.value,
        lamp: this.lampDataList,
        groupFilterIds: this.f.get('groupFilter')?.value,
      });

      console.log('new device id:', newDevice.id);

      //Find the corresponding group filter and insert the designation
      if (this.f.get('groupFilter')?.value != null) {
        let groupFilterIds = this.f.get('groupFilter')?.value as string[];
        let groupFilterList =
            SystemUtils.getGroupFilterListFromLocalStorage();
        groupFilterIds.forEach((selectedFilterId) => {
          let groupFilterList =
            SystemUtils.getGroupFilterListFromLocalStorage();
            const groupFilter = groupFilterList.find(
            (groupFilter) => groupFilter.id === selectedFilterId
          ) as GroupFilter;
          groupFilter.equipmentIds.push(newDevice.id as string);
        });
        SystemUtils.saveGroupFilterListToLocalStorage(groupFilterList);
      }

      //TODO: replace
      equipments.push(newDevice);

      equipment_list.push(newEquipment);
      let selectedSystemDetail = setting.detail.findIndex((s) => s.systemID === this.f.get('systemId')?.value);
      if (selectedSystemDetail != -1) {
        //update the setting => saving to local
        setting.detail[selectedSystemDetail].equipments = equipment_list;
      } else {
        //for testing purpose. No matched systemID case
        let newSystemDetail = new SystemDetail({
          systemID: this.f.get('systemId')?.value,
          system_EN: "System A (EN)",
          system_CN: "System A (TC)",
          system_SC: "System A (SC)",
          equipments: equipment_list,
        })
        setting.detail.push(newSystemDetail);
      }
    } else {
      //Edit existing system
      let currentSystemIndex = equipments.findIndex(
        (s) => s.id === this.device.id
      );
      equipments[currentSystemIndex] = new SampleDeviceData({
        id: this.device.id,
        floor: this.f.get('floor')?.value,
        designation: this.f.get('designation')?.value,
        nameEN: this.f.get('nameEN')?.value,
        nameTC: this.f.get('nameTC')?.value,
        nameSC: this.f.get('nameSC')?.value,
        order: currentSystemIndex + 1,
        systemId: this.f.get('systemId')?.value,
        lamp: this.lampDataList,
        groupFilterIds: this.f.get('groupFilter')?.value,
      });

      //Find the corresponding group filter and insert the designation
      if (this.f.get('groupFilter')?.value != null) {
        let groupFilterIds = this.f.get('groupFilter')?.value as string[];
        let groupFilterList =
            SystemUtils.getGroupFilterListFromLocalStorage();
        groupFilterIds.forEach((selectedFilterId) => {
          const groupFilter = groupFilterList.find(
            (groupFilter) => groupFilter.id === selectedFilterId
          ) as GroupFilter;
          groupFilter.equipmentIds.push(equipments[currentSystemIndex].id as string);
        });
        SystemUtils.saveGroupFilterListToLocalStorage(groupFilterList);
      }
    }

    //TODO: replace
    EquipmentUtils.saveListToLocalStorage(equipments);

    EquipmentUtils.saveEquipmentSettingToLocalStorage(setting);

    if (this.device == null) {
      this.openSubmittedDialog();
    } else {
      this.location.back();
    }
  }

  resetFormData() {
    this.lampDataList = [];
    this.f.reset();
  }

  getSystemPlaceholder() {
    if (this.filterBySystem(this.systemList).length == 0) {
      return 'No Systems Available. Please add a System';
    } else {
      return 'Select System';
    }
  }

  getGroupFilterPlaceholder() {
    if (this.filterBySystem(this.groupFilterList).length == 0) {
      return 'No Group Filters Available in this System';
    } else {
      return 'Select Group Filters';
    }
  }

  remove(device: SampleDeviceData) {
    this.confirmDialogRef = this.dialog.open(
      ConfirmDeleteSystemDialogComponent,
      {
        width: '450px',
        data: 'equipment',
      }
    );

    this.confirmDialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
      if (result != null && result['confirm'] == true) {
        let equipments = EquipmentUtils.getListFromLocalStorage();

        //remove designation in each group filter
        const equipment = equipments.find((e) => e.id === device.id);
        equipment.groupFilterIds?.forEach((groupFilterId) => {
          let groupFilterList =
            SystemUtils.getGroupFilterListFromLocalStorage();
          const groupFilter = groupFilterList.find(
            (groupFilter) => groupFilter.id === groupFilterId
          );
          groupFilter.equipmentIds.splice(
            groupFilter.equipmentIds.indexOf(device.id),
            1
          );
        })
        //remove the selected object in equipment list
        const index = equipments.findIndex((e) => e.id === device.id);
        equipments.splice(index, 1);

        EquipmentUtils.saveListToLocalStorage(equipments);
        this.location.back();
      }
    });
  }

  navigateTo(path: string) {
    this.router.navigateByUrl(path);
  }

  backToLastPage() {
    this.location.back();
  }
}
